<template>
	<div class="wg_topay">
		<div class="wg_topay_header"><i class="icon-arrow_lift"></i></div>
		<div class="wg_topay_conter">
			<div class="head">
				<div class="pay_time">
					<span class="text"></span>
					<span class="time">{{}}</span>
				</div>
				<p class="pay_mes">
					<span class="shop_name">{{shopName}}</span>
					<span class="price">{{orderPrice}}</span>
				</p>
			</div>
			<div class="paytypes">
				<div class="title"></div>
				<ul class="pay_list" v-if="payTypes && payTypes.length">
					<li class="pay_item" v-for="pay in payTypes">
						<div class="type" :style="'background-image:url('+pay.img+')'">{{pay.name}}</div>
						<i class="icon-check_circle"></i>
					</li>
				</ul>
			</div>
		</div>
		<div class="wg_topay_footer">{{orderPrice}}</div>
	</div>
</template>

<script>
	import storageObj from '../../utils/storage.js';
	export default{
		data(){
			return {
				payTime:15,
				shopName:"",
				orderPrice:32,
				payTypes:[{
					name:"",
					img:"pay1.png"
				},{
					name:"",
					img:"pay2.png"
				},{
					name:"",
					img:"pay3.png"
				},{
					name:"",
					img:"pay4.png"
				},{
					name:"QQ",
					img:"pay5.png"
				}]
			}
		},
		methods:{},
		created(){}
	}
</script>

<style lang="scss" scoped>
	.wg_topay{}
</style>


